nav{
    display: flex;
    justify-content: center;
    background-color: black;
    color: white;
    padding: 22px;
}
nav button{
    border: none;
    outline: none;
    background-color: transparent;
    color: white;
    margin-inline: 32px;
    font-size: 22px;
    cursor: pointer;
}
nav button:hover{
    color: rgb(0, 201, 201);
}
.container img{
    width: 250px;
    height: 200px;
}
.container{
    display: flex;
    flex-wrap: wrap;
    width: 900px;
    margin: auto;
    margin-top: 55px;
}
.container .card{
    margin-inline: 11px;
    margin-top: 22px;
    background-color: gray;
    padding: 11px;
}